@import url(https://fonts.googleapis.com/css2?family=Arimo:wght@400;700&display=swap);
html,
body,
#root {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-size: 13px !important;
}

.text-content h1,.text-content h2,.text-content h3,.text-content h4,.text-content h5,.text-content h6{font-family:"Arimo", sans-serif;font-weight:700}.text-content p,.text-content a,.text-content b,.text-content ul,.text-content li{font-family:"Arimo", sans-serif}.text-content span{font-family:"Arimo", sans-serif;font-weight:700;font-style:italic}.text-content b::before{white-space:pre;margin-top:5px}.text-content b::after{margin-bottom:1px}.full-width-image{height:250px;width:250px;top:0;left:0}.image-text{margin:auto;padding-top:5px}.full-screen{-webkit-flex-direction:column;flex-direction:column;margin:1rem 1rem 0 0.5rem;top:0;width:-webkit-fill-available;width:stretch}.centered{text-align:center;margin:auto}

